body {
  font-family: 'PFBeauSansPro', sans-serif;
  font-size: 17px;
  line-height: 1;
  font-weight: 400;
  color: #1778bb;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  max-width: 100%;
}
p,
li {
  line-height: 1.2;
}
p:last-child,
li:last-child {
  margin-bottom: 0;
}
.header {
  background-color: #fff;
}
.header__top {
  padding: 10px 0 13px;
}
.header__bottom {
  border-top: 1px solid #e9ebf0;
  border-bottom: 1px solid #e9ebf0;
}
.header__wrap {
  display: flex;
  align-items: center;
  position: relative;
}
.header .logo svg {
  width: 192px;
  height: 54px;
}
.header .phone {
  font-size: 19px;
  font-weight: 700;
  color: #0069b4;
  margin-left: auto;
}
.header .btn {
  background-color: #0069b4;
  color: #fff;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 700;
  padding: 9px 14px;
  border-radius: 100px;
  margin-left: 20px;
}
.header .sites {
  margin-left: 26px;
  position: relative;
}
.header .sites__toggle {
  font-size: 13px;
  font-weight: 400;
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}
.header .sites__toggle:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 20px;
  height: 20px;
  background-image: url('../img/icons/blue-arrow.png');
  background-size: 8px 5px;
  background-position: center;
  background-repeat: no-repeat;
  border: 2px solid #0069b4;
  border-radius: 100px;
  transition: 0.3s;
}
.header .sites__toggle.active:before {
  transform: rotate(180deg);
  transition: 0.3s;
}
.header .sites__content {
  display: none;
  position: absolute;
  top: calc(100% + 10px);
  left: 0;
  width: 100%;
  background-color: #1778bb;
  padding: 20px;
  z-index: 3;
}
.header .sites__item {
  color: #fff;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  display: block;
}
.header .sites__item:not(:last-of-type) {
  margin-bottom: 20px;
}
.header .socials {
  display: flex;
  margin-left: 24px;
}
.header .socials__item {
  width: 36px;
  height: 36px;
}
.header .socials__item:not(:first-of-type) {
  margin-left: 8px;
}
.header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .nav__item {
  color: #0069b4;
  font-weight: 700;
  font-size: 13px;
  text-transform: uppercase;
  padding: 18px 20px;
  text-align: center;
  position: relative;
  border-left: 1px solid #e9ebf0;
  width: 100%;
}
.header .nav__item.green {
  color: #1db479;
}
.header .nav__item.current {
  background: #e9ebf0;
}
.header .nav__item:last-of-type {
  border-right: 1px solid #e9ebf0;
}
.header .burger {
  position: relative;
  display: none;
  right: 0;
  top: 0;
  width: 25px;
  height: 17px;
  z-index: 6;
  border-radius: 100px;
  margin-left: 15px;
}
.header .burger:before {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 0 #243970;
  border-radius: 5000px;
  transition: 1s;
  width: 2px;
  height: 2px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  display: block;
}
.header .burger.active:before {
  box-shadow: 0 0 0 5000px #243970;
}
.header .burger.active span {
  background-color: #fff;
}
.header .burger.active span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 7px;
}
.header .burger.active span:nth-of-type(2) {
  opacity: 0;
}
.header .burger.active span:nth-of-type(3) {
  transform: rotate(-45deg);
  bottom: 7px;
}
.header .burger span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 3px;
  background-color: #1778bb;
  display: block;
  border-radius: 100px;
  transition: 0.5s;
  transform: rotate(0);
  opacity: 1;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
.header .burger svg {
  display: none;
  fill: #fff;
  width: 20px;
  height: 20px;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  margin: auto;
}
.header .mobile-menu {
  position: fixed;
  top: 70px;
  right: 0;
  height: calc(100vh - 50px);
  overflow: auto;
  width: 100%;
  z-index: 6;
  padding: 0 40px 40px;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-20px);
  transition: 0.5s;
  transition-delay: 0.2s;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.header .mobile-menu.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.header .mobile-menu .nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 0 20px;
  padding: 0;
}
.header .mobile-menu .nav__item {
  border: none;
  color: #fff;
  padding: 5px 0;
  position: relative;
  width: auto;
}
.header .mobile-menu .nav__item.green {
  color: #1db479;
}
.header .mobile-menu .nav__item.green.current {
  background-color: transparent;
}
.header .mobile-menu .nav__item.green.current:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 1px;
  width: 100%;
  height: 2px;
  border-radius: 100px;
  background-color: #1db479;
}
.header .mobile-menu .nav__item.current {
  background-color: transparent;
}
.header .mobile-menu .nav__item.current:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 1px;
  width: 100%;
  height: 2px;
  border-radius: 100px;
  background-color: #fff;
}
.header .mobile-menu .phone {
  margin-left: 0;
  margin-bottom: 10px;
  color: #fff;
  display: block;
}
.header .mobile-menu .btn {
  margin-left: 0;
  margin-bottom: 20px;
  display: block;
}
.header .mobile-menu .sites {
  margin-left: 0;
  margin-bottom: 20px;
  display: block;
}
.header .mobile-menu .sites__toggle {
  color: #fff;
}
.header .mobile-menu .sites__toggle:before {
  background-image: url('../img/icons/white-arrow.png');
  border: 2px solid #fff;
}
.header .mobile-menu .sites__content {
  display: none;
  position: static;
  background: none;
  padding: 20px 30px;
}
.header .mobile-menu .sites__item {
  color: #fff;
}
.header .mobile-menu .sites__item:not(:last-of-type) {
  margin-bottom: 10px;
}
.header .mobile-menu .socials {
  margin-left: 0;
  display: block;
}
@media screen and (max-width: 1200px) {
  .header .phone {
    font-size: 15px;
  }
  .header .btn {
    font-size: 12px;
  }
  .header .sites__toggle {
    font-size: 12px;
  }
  .header .nav__item {
    font-size: 12px;
    padding: 18px 25px;
    white-space: nowrap;
    width: 100%;
  }
}
@media screen and (max-width: 992px) {
  .header {
    border-bottom: 1px solid #e9ebf0;
  }
  .header__bottom {
    display: none;
  }
  .header .logo svg {
    width: 150px;
    height: 43px;
  }
  .header .phone {
    font-size: 12px;
  }
  .header .btn {
    margin-left: 15px;
    padding: 6px 10px 5px;
    font-size: 10px;
  }
  .header .sites {
    margin-left: 15px;
  }
  .header .socials {
    margin-left: 15px;
  }
  .header .burger {
    display: block;
  }
  .header .nav {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .header .phone {
    display: none;
  }
  .header .btn {
    display: none;
  }
  .header .sites {
    display: none;
  }
  .header .socials {
    display: none;
  }
  .header .burger {
    margin-left: auto;
  }
}
.footer {
