.popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 40px 15px;
  background-color: rgba(0, 0, 0, 0.7);
  overflow: auto;
  display: none;
}
.popup__wrap {
  background-color: #fff;
  max-width: 510px;
  width: 100%;
  margin: 0 auto;
  padding: 69px 60px 62px;
  position: relative;
}
.popup__wrap .close {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 43px;
  right: 32px;
  cursor: pointer;
}
.popup__wrap .close:before,
.popup__wrap .close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 11px;
  height: 2px;
  background-color: #1778bb;
}
.popup__wrap .close:before {
  transform: rotate(-45deg);
}
.popup__wrap .close:after {
  transform: rotate(45deg);
}
.popup__wrap .title {
  font-size: 25px;
  font-weight: 800;
  margin: 0 0 40px;
  color: #243970;
}
.popup__wrap .wysiwyg p,
.popup__wrap .wysiwyg ul,
.popup__wrap .wysiwyg ol {
  font-size: 15px;
  color: #243970;
}
.popup__wrap .wysiwyg ul {
  list-style: none;
  padding: 0;
}
.popup__wrap .wysiwyg ul li {
  position: relative;
  padding-left: 15px;
}
.popup__wrap .wysiwyg ul li:before {
  content: '';
  position: absolute;
  left: 0;
  top: 7px;
  width: 6px;
  height: 6px;
  border-radius: 100px;
  background-color: #243970;
}
.popup__wrap .wysiwyg ol {
  list-style: none;
  padding: 0;
  counter-reset: item;
}
.popup__wrap .wysiwyg ol li {
  position: relative;
  padding-left: 15px;
  counter-increment: item;
}
.popup__wrap .wysiwyg ol li:before {
  content: counter(item) ".";
  position: absolute;
  left: 0;
  top: 0;
  width: 6px;
  height: 6px;
  border-radius: 100px;
  background-color: #243970;
}
.popup__wrap .wysiwyg a {
  text-decoration: underline;
}
@media screen and (max-width: 992px) {
  .popup__wrap {
    padding: 40px;
  }
  .popup__wrap .close {
    top: 15px;
    right: 15px;
  }
  .popup__wrap .title {
    font-size: 20px;
    margin: 0 0 20px;
  }
}
@media screen and (max-width: 768px) {
  .popup__wrap {
    padding: 30px 20px;
  }
