.product .wrapper {
  max-width: 1140px;
}
.product__head {
  padding: 37px 7px 40px;
}
.product__head .close {
  position: relative;
  width: 33px;
  height: 33px;
  display: block;
  margin-left: auto;
}
.product__head .close:before,
.product__head .close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 4px;
  border-radius: 100px;
  background-color: #243970;
}
.product__head .close:before {
  transform: rotate(45deg);
}
.product__head .close:after {
  transform: rotate(-45deg);
}
.product__intro {
