  font-size: 25px;
  margin: 0;
  margin-bottom: 24px;
  font-weight: 400;
}
.product__back {
  padding: 73px 0;
  text-align: center;
}
.product__back a {
  background-color: #243970;
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  border-radius: 100px;
  padding: 13px 29px;
  display: inline-block;
}
.product .frame {
  margin: 109px 0 62px;
}
.product .frame__window {
  position: relative;
  padding: 4px;
  max-width: 926px;
  margin: 0 auto;
  pointer-events: none;
}
.product .frame__window:before {
  content: '';
  position: absolute;
  left: -31px;
  top: -31px;
  width: calc(100% + 62px);
  height: calc(100% + 62px);
  border: 7px solid #e9ebf0;
  border-radius: 30px;
}
.product .frame__window:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 4px solid #e9ebf0;
}
.product .frame__foot {
  border: 7px solid #e9ebf0;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  margin-top: 76px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  height: 60px;
  max-width: 436px;
}
.product .frame__foot:before,
.product .frame__foot:after {
  content: '';
  position: absolute;
  width: 7px;
  height: 60px;
  background-color: #e9ebf0;
}
.product .frame__foot:before {
  left: 93px;
  bottom: calc(100% + 1px);
  transform-origin: bottom;
  transform: rotate(20deg);
}
.product .frame__foot:after {
  right: 93px;
  bottom: calc(100% + 1px);
  transform-origin: bottom;
  transform: rotate(-20deg);
}
.product .slider {
  pointer-events: all;
}
.product .slider .slick-arrow {
