.photos {
    .slider {
        &__item {
            .image {
                position: relative;
                padding-top: 100%;

                img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            
        }
    }
}